/* Основной контейнер */
.calculator-container {
  min-height: 100vh;
  background: linear-gradient(to right, #e0ecff, #ffffff);
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 2rem;
  font-family: 'Segoe UI', sans-serif;
}

/* Карточка */
.calculator-box {
  background-color: #fff;
  padding: 2rem;
  border-radius: 1.5rem;
  box-shadow: 0 8px 24px rgba(0, 0, 0, 0.12);
  max-width: 400px;
  width: 100%;
}

/* Заголовок */
.calculator-title {
  font-size: 1.75rem;
  font-weight: bold;
  color: #1e3a8a;
  text-align: center;
  margin-bottom: 1.5rem;
}

/* Инпуты */
.calculator-input {
  width: 100%;
  border: 1px solid #ccc;
  padding: 0.6rem;
  border-radius: 0.5rem;
  font-size: 1rem;
  margin-bottom: 1rem;
}

/* Кнопка */
.calculate-button {
  width: 100%;
  background-color: #2563eb;
  color: white;
  padding: 0.75rem;
  font-weight: 600;
  border: none;
  border-radius: 0.5rem;
  cursor: pointer;
  transition: background 0.3s ease;
}

.calculate-button:hover {
  background-color: #1d4ed8;
}

/* Результат */
.result-box {
  background-color: #f9fafb;
  border: 1px solid #e5e7eb;
  padding: 1rem;
  margin-top: 1.5rem;
  border-radius: 1rem;
  font-size: 0.95rem;
}

/* Итоговая цена */
.result-final {
  font-size: 1.25rem;
  color: #059669;
  font-weight: bold;
  margin-top: 0.5rem;
}

/* Дисклеймер */
.disclaimer {
  font-size: 0.75rem;
  color: #6b7280;
  text-align: center;
  margin-top: 2rem;
  line-height: 1.2;
}
